import angular from 'angular';

class ApiService {
	constructor($http) {
		"ngInject";
  		this.$http = $http;
  		this.apiendpoint = "http://localhost:8080/v1";
	}

	getSlot() {
  		return this.$http.get(this.apiendpoint + "/slot")
    	.then(response => response.data);
	}

	getLocation() {
  		return this.$http.get(this.apiendpoint + "/location")
    	.then(response => response.data);
	}

	getStudent() {
  		return this.$http.get(this.apiendpoint + "/student")
    	.then(response => response.data);
	}

	getLecturer() {
  		return this.$http.get(this.apiendpoint + "/lecturer")
    	.then(response => response.data);
	}

	getTerm() {
  		return this.$http.get(this.apiendpoint + "/term")
    	.then(response => response.data);
	}

	getCourse() {
  		return this.$http.get(this.apiendpoint + "/course")
    	.then(response => response.data);
	}

	// Timetable
	getAllTimetable() {
		return this.$http.get(this.apiendpoint + "/timetable")
		.then(response => response.data);
	}

	getTimetableByTerm(termId) {
		return this.$http.get(this.apiendpoint + "/timetable/term/"+termId)
		.then(response => response.data);
	}

	getTimetableByCourse(courseId) {
		return this.$http.get(this.apiendpoint + "/timetable/course/"+courseId)
		.then(response => response.data);
	}

	getCourseByTerm(termId) {
		return this.$http.get(this.apiendpoint + "/course/term/"+termId)
		.then(response => response.data);	
	}

  	formatDate(date) {
	    var hours = date.getHours();
	    var minutes = date.getMinutes();
	    var seconds = date.getSeconds();
	    var strTime = hours + ':' + minutes + ':' + seconds;
	    return date.getMonth()+1 + "/" + date.getDate() + "/" + date.getFullYear() + " " + strTime;
  	}
}

ApiService.$inject = ['$http', '$q', '$location', 'myConfig'];

export default ApiService;


