class TimetableController {
  	constructor($scope, $rootScope, myConfig, $http, ApiService) {
	  	"ngInject";
	    this.name = 'timetable';
	    this.$rootScope = $rootScope;
	    this.$http = $http;
	    this.ApiService = ApiService;
	    this.apiendpoint = myConfig.ApiEndpoint;

	  	let _this = this;

	  	this.ApiService.getTerm().then(data => this.termList = data);

	  	this.ApiService.getCourse().then(data => this.courseList = data);
	  	this.ApiService.getAllTimetable().then(data => this.timetables = data);
  	}

  	changeTerm() {
      if (this.termid != null) {
        this.ApiService.getCourseByTerm(this.termid.id).then(data => this.courseList = data);  
      } else {
        this.ApiService.getCourse().then(data => this.courseList = data);
      }
  		
  	}

  	timetableFilter() {
      if (typeof this.course !== "undefined" && this.course != null) {
        this.ApiService.getTimetableByCourse(this.course.id).then(data => this.timetables = data);
      } else {
        if (typeof this.termid !== "undefined" && this.termid != null) {
          this.ApiService.getTimetableByTerm(this.termid.id).then(data => this.timetables = data);
        } else {
          this.ApiService.getAllTimetable().then(data => this.timetables = data);
        }
      }
  	}
}

export default TimetableController;



