class CreateTimetableController {
  constructor($http, myConfig, ApiService) {
    "ngInject";
    
    this.$http = $http;
    this.apiendpoint = myConfig.ApiEndpoint;
    this.ApiService = ApiService;

    this.ApiService.getCourse().then(data => this.courseList = data);
    this.ApiService.getSlot().then(data => this.slotList = data);
    this.ApiService.getLocation().then(data => this.locationList = data);
    this.ApiService.getLecturer().then(data => this.lecturerList = data);
  }

  addTimetable() {

  }

  createTimetable() {
		let _this = this;
		var config = {
			headers: {'Content-Type': 'application/x-www-form-urlencoded'}
		}

		//var startDateConvert = this.dt1.getFullYear() + '-' + (this.dt1.getMonth() + 1) + '-' + this.dt1.getDate();
	    //var endDateConvert = this.dt2.getFullYear() + '-' + (this.dt2.getMonth() + 1) + '-' + this.dt2.getDate();
	    var data = {courseId: parseInt(this.course.id), slotId: parseInt(this.slot.id), locationId: parseInt(this.location.id), lecturerId: parseInt(this.lecturer.id)};
	    //console.log(data);
	    this.$http.post(this.apiendpoint + "/timetable", data, config)
	      .then(function (response) {
	        if (response.status == 201) {
				var rlocation = "/timetable";
				window.location = rlocation;
	        }
	    });
	}
}

export default CreateTimetableController;



