class RegisterController {
  	constructor($scope, myConfig, $http, ApiService) {
	  	"ngInject";
	  	this.$http = $http;
	    
	    this.ApiService = ApiService;
	    this.apiendpoint = myConfig.ApiEndpoint;

	    /*$scope.changeLanguage = function (key) {
	    	$translate.use(key);
	  	};*/
	  	let _this = this;
	  	this.ApiService.getCourse().then(data => this.courseList = data);
	  	this.ApiService.getStudent().then(data => this.studentList = data);
	  	//this.test = "ahihihi";
  	}

  	createRegistration() {
		let _this = this;
		var config = {
			headers: {'Content-Type': 'application/x-www-form-urlencoded'}
		}

	    var data = {courseId: parseInt(this.course.id), studentId: parseInt(this.student.id)};
	    
	    this.$http.post(this.apiendpoint + "/courseregistration", data, config)
	      .then(function (response) {
	        if (response.status == 200) {
				alert(response.data.message);
	        }
	    });
	}
}

export default RegisterController;



