class HomeController {
  constructor($scope, $rootScope, myConfig, $http, $cookies, ApiService) {
    "ngInject";
    this.name = 'home';
    this.$rootScope = $rootScope;
    this.$http = $http;
    this.$cookies = $cookies;
    this.apiendpoint = myConfig.ApiEndpoint;
    this.ApiService = ApiService;

    let _this = this;
    this.ApiService.getTerm().then(data => this.termList = data);
    this.getTerms().then(data => this.terms = data);
    this.getCourses().then(data => this.courseList = data);
  }

  getStudentTimetable() {
    return this.$http({
      url: this.apiendpoint + '/timetable/student/' + this.$rootScope.globals.currentUser.username,
      method: "GET",
      headers: {}
    }).then(response => response.data);
  }

  getCourses() {
    return this.$http.get(this.apiendpoint + "/course")
    .then(response => response.data);
  }

  getTerms() {
    return this.$http.get(this.apiendpoint + "/term")
    .then(response => response.data);
  }

  deleteCourse(cId) {
    let _this = this;
    var r = confirm("Are you sure you want to delete this course?");
    if (r == true) {
      let _this = this;
      this.$http.delete(this.apiendpoint + "/course/"+cId)
      .then(function(response) {
        if (response.status == 200) {
          _this.getCourses().then(data => _this.courseList = data);
        }
      })
    }
  };

  courseFilter() {
      if (typeof this.termid !== "undefined" && this.termid != null) {
        this.ApiService.getCourseByTerm(this.termid.id).then(data => this.courseList = data);
      } else {
        this.getCourses().then(data => this.courseList = data);
      }
    }
}

export default HomeController;


