import angular from 'angular';
import uiRouter from 'angular-ui-router';
import createTimetableComponent from './createTimetable.component';

let createTimetableModule = angular.module('createTimetable', [
  uiRouter
])

.component('createTimetable', createTimetableComponent)

.config(($stateProvider, $urlRouterProvider) => {
  "ngInject";

  $urlRouterProvider.otherwise('/');

  $stateProvider
    .state('createTimetable', {
      url: '/timetable/create',
      component: 'createTimetable'
    });
})

.name;

export default createTimetableModule;



