class CreateController {
  constructor($http, myConfig, ApiService) {
  	"ngInject";
    //this.name = 'create';

    this.$http = $http;
    this.apiendpoint = myConfig.ApiEndpoint;
    this.ApiService = ApiService;

    this.ApiService.getTerm().then(data => this.termList = data);

    this.today = function() {
    	this.dt = new Date();
  	};
  	this.today();
  	this.clear = function() {
    	this.dt = null;
  	};
  	this.inlineOptions = {
    	customClass: this.getDayClass,
    	minDate: new Date(),
    	showWeeks: true
  	};
  	this.dateOptions = {
	    dateDisabled: this.disabled,
	    formatYear: 'yy',
	    maxDate: new Date(2020, 5, 22),
	    minDate: new Date(),
	    startingDay: 1
  	};

  	this.formats = ['dd-MMMM-yyyy', 'yyyy-MM-dd', 'yyyy-MM-ddTHH:mm:ss', 'shortDate'];
  	this.format = this.formats[1];
  	//this.altInputFormats = ['M!/d!/yyyy'];
  	this.altInputFormats = ['dd/mm/yyyy'];

  	this.popup1 = {
    	opened: false
  	};

  	this.popup2 = {
    	opened: false
  	};

  	let tomorrow = new Date();
  	tomorrow.setDate(tomorrow.getDate() + 1);
  	var afterTomorrow = new Date();
  	afterTomorrow.setDate(tomorrow.getDate() + 1);
  	this.events = [
    {
      date: tomorrow,
      status: 'full'
    },
    {
      date: afterTomorrow,
      status: 'partially'
    	}
  	];
  }

  createCourse() {
		let _this = this;
		var config = {
			headers: {'Content-Type': 'application/x-www-form-urlencoded'}
		}

		var startDateConvert = this.dt1.getFullYear() + '-' + (this.dt1.getMonth() + 1) + '-' + this.dt1.getDate();
	    var endDateConvert = this.dt2.getFullYear() + '-' + (this.dt2.getMonth() + 1) + '-' + this.dt2.getDate();
	    var data = {class: this.class, name: this.name, courseId: this.courseid, quota: parseInt(this.quota), termId: parseInt(this.termid.id),startDate: startDateConvert, endDate: endDateConvert, status: this.status};
	    
	    this.$http.post(this.apiendpoint + "/course", data, config)
	      .then(function (response) {
	        if (response.status == 201) {
				var rlocation = "/";
				window.location = rlocation;
	        }
	    });
	}

  	// Disable weekend selection
  	disabled(data) {
    	var date = data.date,
      	mode = data.mode;
    	return mode === 'day' && (date.getDay() === 0 || date.getDay() === 6);
  	}

  	toggleMin() {
    	this.inlineOptions.minDate = this.inlineOptions.minDate ? null : new Date();
    	this.dateOptions.minDate = this.inlineOptions.minDate;
  	};

  	open1() {
   		this.popup1.opened = true;
  	};
  	open2() {
   		this.popup2.opened = true;
  	};

  	setDate = function(year, month, day) {
    	this.dt = new Date(year, month, day);
  	};

  	getDayClass(data) {
    var date = data.date,
      mode = data.mode;
    if (mode === 'day') {
      var dayToCheck = new Date(date).setHours(0,0,0,0);

      for (var i = 0; i < this.events.length; i++) {
        var currentDay = new Date(this.events[i].date).setHours(0,0,0,0);

        if (dayToCheck === currentDay) {
          return this.events[i].status;
        }
      }
    }

    return '';
  }
}

export default CreateController;



